.header {
  width: 1280px;
  height: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 29px auto 0;
  padding: 0;
}

@media screen and (min-width: 768px) and (max-width: 1366px) {
  .header {
    width: 639px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header {
    margin-top: 14px;
    width: 280px;
  }
}