.menu__image {
  width: 625px;
  height: 625px;
}

@media screen and (min-width: 768px) and (max-width: 1366px) {
  .menu__image {
    width: 568px;
    height: 568px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .menu__image {
    width: 278px;
    height: 278px;
  }
}